import axios from 'axios'
import createAuthRefreshInterceptor from 'axios-auth-refresh'
import router from '@/router'
import tokens from '@/store/tokens'
const url = 'https://6600e36b-4ecc-473b-ac50-d7795256b092.app.jexia.com'

let api = axios.create({
    baseURL: url,
    timeout: 5000
})

const refreshAuthLogic = failedRequest => 
    axios.post(url + '/auth/refresh', { refresh_token: tokens.getRefreshToken() })

    .then(tokenRefreshResponse => {
        tokens.setTokens(tokenRefreshResponse.data)
        failedRequest.response.config.headers['Authorization'] = 'Bearer ' + tokens.getAccessToken()
        return Promise.resolve()
    })

    .catch(error => {
        router.push('/login')
        Promise.reject(error)
    })

createAuthRefreshInterceptor(api, refreshAuthLogic)

api.interceptors.request.use(config => {
    const access_token = tokens.getAccessToken()

    if(access_token) {
        config.headers['Authorization'] = 'Bearer ' + access_token
    }
    
    return config
}, error => {
    Promise.reject(error)
})

export default api